@import url(https://fonts.googleapis.com/css?family=Coming+Soon&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body * {
  font-family: 'Coming Soon', cursive !important; }

body {
  background-color: #ffff; }

.d-flex {
  display: flex; }

.fl-wrap {
  flex-wrap: wrap; }

.fl-col {
  flex-direction: column; }

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.catgrid {
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  border: 5px solid #3f51b5;
  padding: 3em;
  border-radius: 30px; }

.catgrid + .MuiButton-containedPrimary {
  color: #fff;
  background-color: #3f51b5;
  margin-top: 20px;
  font-size: 1.5em; }

#root .MuiOutlinedInput-inputSelect {
  padding-right: 36px; }

a .MuiAvatar-root {
  width: 100px;
  border: 5px solid pink;
  cursor: pointer;
  height: 100px;
  margin: 5px;
  border-radius: 50%;
  overflow: hidden; }

a .MuiAvatar-root + svg {
  width: 80px;
  height: 80px; }

a .MuiAvatar-root img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  text-align: center;
  pointer-events: none; }

a .MuiAvatar-root:hover {
  border-color: #bfffbf;
  /* border-width: 7px; */
  opacity: 0.8; }

.MuiCardHeader-root + svg {
  display: flex;
  width: 200px;
  margin: auto; }

.favouriteslist svg {
  width: 150px;
  height: 150px;
  position: absolute;
  right: 0; }

.dislike {
  cursor: pointer;
  margin: 10px; }

.dislike svg {
  fill: darkred;
  width: 2em; }

